import React from 'react';
import styles from './Logo.module.css';
const Logo = props => (
  <div className={styles.Logo}>
  <img src='https://cdn.glitch.com/9c855d83-2b00-48ba-a3ef-508be20de7c0%2Fburger-logo.png?1521235823313' alt="logo" />
  </div>
  )

export default Logo;


